@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Montserrat', sans-serif;
  font-size: 62.5%;
  position: relative;
  background-color: #edf2f7;
}

input, textarea, p, a, button {
  outline: none;
  border: none;
}

.App {
  padding: 15px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  position: absolute;
  width: 100%;
  box-sizing: border-box;
  min-height: 100vh;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-height: 45px;
  background-color: #e2e8f0;
  border-radius: 5px;
  margin-bottom: .7rem;
  padding: 8px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  position: relative;
}

.header__button {
  position: relative;
  width: 33px;
  height: 33px;
  background-color: inherit;
  margin-left: 15px;
}

.header__button--plus {
  width: inherit;
  height: inherit;
  left: 0;
  top: 0;
  position: absolute;
  fill: #a0aec0;
}

.header__button--plus:hover {
  fill: #4a5568;
}

.header__logout a {
  font-size: 1rem;
  color: #4A5568;
  text-decoration: none;
}

.header__wrapper-input {
  position: relative;
  margin-right: 10px;
}

.header__wrapper-input input {
  padding-left: 20px;
  border-radius: 5px;
  padding: 12px;
  padding-left: 45px;
  width: 220px;
  display: flex;
  position: relative;
  font-size: 1rem;
  background-color: #edf2f7;
}

.header__wrapper-input .search {
  position: absolute;
  fill: #a0aec0;
  z-index: 1;
  margin-left: 10px;
  width: 16px;
  height: 16px;
  top: 50%;
  -webkit-transform: translate(0%, -50%);
          transform: translate(0%, -50%);
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
  .header input {
    width: 180px;
  }
}

/* Small devices (landscape phones, 576px and up)*/
/* Medium devices (tablets, 768px and up)*/
/* Large devices (desktops, 992px and up)*/
/* Extra large devices (large desktops, 1200px and up)*/
.todos__wrappers {
  display: flex;
  flex-wrap: wrap;
  background-color: #edf2f7;
  margin-left: -.5rem;
  margin-right: -.5rem;
  position: relative;
}

.todos__wrappers .todo__wrapper {
  width: 25%;
  height: 250px;
  box-sizing: border-box;
  padding: .5rem;
  border-radius: 5px;
}

.todos__wrappers .todo__wrapper .todo {
  width: 100%;
  height: 100%;
  border-radius: 5px;
  background-color: #ffff;
  padding: .5rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}

.todos__wrappers .todo__wrapper .todo__title {
  display: flex;
  justify-content: space-between;
}

.todos__wrappers .todo__wrapper .todo__title-text {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 1rem;
  color: #2d3748;
  margin: 0;
  word-break: break-all;
  width: 90%;
}

.todos__wrappers .todo__wrapper .todo__title-button {
  width: 32px;
  height: 32px;
  background-color: #c6f6d5;
  border: none;
  border-radius: 50%;
  display: block;
  margin-left: 1rem;
}

.todos__wrappers .todo__wrapper .todo__title-button .checkbox {
  display: flex;
  width: 16px;
  height: 16px;
  margin: 0 auto;
  fill: #38a169;
}

.todos__wrappers .todo__wrapper .todo__title-button.completed {
  background-color: #f7fafc;
}

.todos__wrappers .todo__wrapper .todo__title-button.completed .checkbox {
  fill: #cbd5e0;
}

.todos__wrappers .todo__wrapper .todo__title-button.completed .checkbox:hover {
  fill: #a0aec0;
}

.todos__wrappers .todo__wrapper .todo__text {
  font-size: 0.8rem;
  flex: 1 1;
  margin-top: 10px;
}

.todos__wrappers .todo__wrapper .todo__control {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.todos__wrappers .todo__wrapper .todo__control span .date-icon {
  width: 15px;
  height: 20px;
  color: #e2e8f0;
}

.todos__wrappers .todo__wrapper .todo__control .todo__control_text {
  font-size: 0.9rem;
  color: #cbd5e0;
  display: flex;
  flex: 1 1;
  align-self: center;
  margin-left: 8px;
}

.todos__wrappers .todo__wrapper .todo__control .todo__button-edit, .todos__wrappers .todo__wrapper .todo__control .todo__button-delete {
  border: none;
  width: 20px;
  height: 20px;
  display: flex;
  background-color: transparent;
}

.todos__wrappers .todo__wrapper .todo__control .todo__button-edit .icon-edit, .todos__wrappers .todo__wrapper .todo__control .todo__button-delete .icon-edit, .todos__wrappers .todo__wrapper .todo__control .todo__button-delete .icon-delete {
  width: 100%;
  height: 100%;
  color: #e2e8f0;
}

.todos__wrappers .todo__wrapper .todo__control .todo__button-edit .icon-edit:hover, .todos__wrappers .todo__wrapper .todo__control .todo__button-delete .icon-edit:hover, .todos__wrappers .todo__wrapper .todo__control .todo__button-delete .icon-delete:hover {
  color: #a0aec0;
}

.todos__wrappers .todo__wrapper .todo__control .todo__button-delete {
  margin-left: 8px;
}

.todos__wrappers .todo__wrapper .todo__control .todo__button-delete .icon-delete:hover {
  color: #a0aec0;
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
  .todos__wrappers {
    box-sizing: content-box;
  }
  .todos__wrappers .todo__wrapper {
    width: 100%;
  }
}

/* Small devices (landscape phones, 576px and up)*/
@media (min-width: 576px) and (max-width: 767.98px) {
  .todos__wrappers .todo__wrapper {
    width: 50%;
  }
}

/* Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 991.98px) {
  .todos__wrappers .todo__wrapper {
    width: 33.3333333%;
  }
}

/* Large devices (desktops, 992px and up)*/
@media (min-width: 992px) and (max-width: 1499.98px) {
  .todos__wrappers .todo__wrapper {
    width: 33.333333%;
  }
}

/* Extra large devices (large desktops, 1200px and up)*/
form {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 450px;
  height: 350px;
  border-radius: 5px;
  box-sizing: border-box;
  padding: 32px;
  display: flex;
  flex-direction: column;
  background-color: #fff;
}

form label {
  font-size: .875rem;
  margin-bottom: .5rem;
  font-weight: 700;
  color: #4a5568;
}

form input {
  border-radius: 5px;
  padding-top: .75rem;
  padding-bottom: .75rem;
  padding-left: 1rem;
  padding-right: 1rem;
  background-color: #edf2f7;
  margin-bottom: 1rem;
  font-size: 1rem;
}

form input::-webkit-input-placeholder {
  font-size: 1rem;
}

form input:-ms-input-placeholder {
  font-size: 1rem;
}

form input::-ms-input-placeholder {
  font-size: 1rem;
}

form input::placeholder {
  font-size: 1rem;
}

form textarea {
  height: 128px;
  border-radius: 5px;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 1rem;
  font-size: 1rem;
  background-color: #edf2f7;
}

form textarea::-webkit-input-placeholder {
  font-size: 1rem;
}

form textarea:-ms-input-placeholder {
  font-size: 1rem;
}

form textarea::-ms-input-placeholder {
  font-size: 1rem;
}

form textarea::placeholder {
  font-size: 1rem;
}

form button {
  text-transform: uppercase;
  padding-top: .75rem;
  padding-bottom: .75rem;
  background-color: #1a202c;
  color: #fff;
  font-weight: 700;
  border-radius: 5px;
  margin-top: 25px;
  font-size: 1rem;
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
  form {
    width: 90%;
  }
}

